// material
import { Button } from '@mui/material';

export default function AddEnquiry() {
  return (
    <Button
      variant="contained"
      sx={{
        mt: 1,
        color: 'white',
        backgroundColor: '#B98900',
        boxShadow: '#916A00',
        px: 5,
        '&:hover': {
          backgroundColor: '#B98900'
        }
      }}
    >
      Add Enquiry
    </Button>
  );
}
